html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;

  font-size: 16px;
  line-height: 22px;
  font-family: 'PT Mono', "Arial", sans-serif;
  font-weight: normal;
  font-style: normal;
  color: $text-color;
  background-color: $white-color;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus) {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
